export const parseAllGoogleSpreadsheetPetitsAnimauxProductsResponse = (graphQlData) => {
  const { allGoogleSpreadsheetPetitsAnimauxProducts } = graphQlData;
  return allGoogleSpreadsheetPetitsAnimauxProducts.edges.map(edge => {
    const product = edge.node;
    const {
      currencyCode,
      description,
      id,
      materials,
      price,
      quantity,
      prices,
      title,
      tags,
      images,
      etsyAffiliateLink,
    } = product;
    const productTags = (tags || "").split(",");

    const variations = [];
    if (product.variation1Name) {
      const variation1 = {
        name: product.variation1Name,
        values: product.variation1Values.split(","),
      };
      variations.push(variation1);
    }
    if (product.variation2Name) {
      const variation2 = {
        name: product.variation2Name,
        values: product.variation2Values.split(","),
      };
      variations.push(variation2);
    }

    const gatsbyImages = [
      product.image1Gatsby,
      product.image2Gatsby,
      product.image3Gatsby,
      product.image4Gatsby,
      product.image5Gatsby,
      product.image6Gatsby,
      product.image7Gatsby,
      product.image8Gatsby,
      product.image9Gatsby,
      product.image10Gatsby,
    ].filter(gatsbyImage => gatsbyImage).map(gatsbyImage => gatsbyImage.childImageSharp.gatsbyImageData);

    return {
      currencyCode,
      etsyAffiliateLink,
      description,
      id,
      materials,
      price: parseFloat(price),
      priceIds: prices ? JSON.parse(prices): [],
      quantity,
      title,
      tags: productTags,
      variations,
      images: images ? JSON.parse(images) : [],
      gatsbyImages,
    };
  });
};
